define('train-with-brett/pods/day/route', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({
    model: function () {
      return this.store.find("day", 1);
    },

    actions: {
      viewDay: function (day) {
        this.transitionTo("day", day);
      }
    }
  });

});